*
{
    margin:0;
    padding:0;
}
nav{
    background-color: navy;
    color:white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
h1{
    color:limegreen;
}
nav ul{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 40%;
}
nav ul a{
    font-size:20px;
    font-weight:300;
    color:white;
    text-decoration:none;
}